body {
  background-color: rgb(11, 153, 129);
}
.profile {
  position: relative;
  bottom: 5em;
}
img {
  width: 150px;
  border: inset 7px;
  border-radius: 50px;
}

.container {
  overflow: hidden;
  border: black 3px solid;
  width: 20%;
  height: 70ch;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: blanchedalmond;
  position: relative;
  left: 40%;
  top: 10em;
}

#Name {
  position: relative;
  right: 10px;
  font-size: 30px;
}
#Skills {
  position: relative;
  left: 10px;
  font-size: 30px;
}
#Likes {
  font-size: 30px;
}
.btn {
  padding: 10px;
}

.fab {
  position: relative;
  top: 1.5em;
  font-size: 50px;
}

.fas {
  position: relative;
  top: 60px;
  font-size: 50px;
}

.popup {
  position: relative;
  bottom: 2em;
  font-size: 25px;
  font-weight: bold;
}

@media only screen and (device-width: 414px) and (device-height: 896px) and (-webkit-device-pixel-ratio: 2) {
  .container {
    overflow: hidden;
    border: black 3px solid;
    width: 70%;
    height: 70ch;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: blanchedalmond;
    position: relative;
    left: 10%;
    top: 3.5em;
  }

  #Name {
    position: relative;
    right: 10px;
    font-size: 15px;
  }
  #Skills {
    position: relative;
    left: 10px;
    font-size: 15px;
  }
}
